div#avatar-container #avatar {
    width: 140px;
}

div#cats {
    position: absolute;
    bottom: 0;
    right: 40px;
    height: 120px;
    transform: rotateY(180deg);
    transition: all 2s ease;
}

div#avatar-container #cats img {
    position: absolute;
}
#ralph {
    width: 120px;
    transform: translateX(600px);
    z-index: 0;
}
#atreyu {
    width: 100px;
    transform: translateX(800px);
    z-index: 1;
    top: 34px;
}

@media screen and (max-width: 640px){
    div#avatar-container #avatar {
        width: 90%;
    }    
    #ralph {
        width: 100px;
    }
    #atreyu {
        width: 80px;
    }
}